@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
.product-details-container {
}
.pd-divider {
  display: flex;
  width: 100%;
}
.pd-divider .pd-left {
  width: 40%;
  padding: 20px;
  position: sticky;
  top: 74px;
  height: fit-content;
  left: 0;
  z-index: 99;
}
/* .pd-divider .pd-left div{
    background-color: rgb(209, 208, 208);
    width: 100%;
    height: 350px;
} */
.pd-divider .pd-right {
  width: 60%;
  padding: 20px;
}
/* .pd-divider .pd-right .pd-right-details{
     width: 100%;
} */

.h2-heading {
  font-size: 25px;
  text-align: center;
  margin: 50px 0 30px;
}


/*************************************************************************************************************/

.pd-divider .pd-right .pd-right-details {
  width: 90%;
}
.product-name {
  font-size: 35px;
  margin-bottom: 25px;
  font-weight: 500;
}
.product-rate-avail {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}
.product-availibility {
  font-weight: 600;
  color: green;
}
.product-rating {
  /* display: flex; */
}
.rating-heading {
  font-weight: 500;
  font-size: 13px;
  padding-left: 3px;
  color: #898c89;
}
.stars-div {
  display: flex;
  align-items: center;
}
.start-b {
  font-size: 21px;
  color: #006ff2;
}
.start-g {
  font-size: 21px;
  color: #aaacac;
}
.ratings-numbers {
  font-weight: 500;
  font-size: 11px;
  padding-left: 3px;
  color: #898c89;
  padding-bottom: 5px;
}
.uni-heading {
  font-weight: 500;
  margin-bottom: 15px;
}
.product-des {
  margin-bottom: 25px;
}
.product-des p {
  font-size: 14px;
}

.product-size {
  margin-bottom: 25px;
  width: 100%;
}
.size-dropdown {
  width: 138px;
  padding: 0px 10px;
  background: #f6f6f6;
  border-radius: 7px;
  font-weight: 600;
  border: none;
  outline: none;
  font-family: inherit;
  font-style: inherit;
  height: 40px;
}

.product-qty-pincode {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 25px;
  flex-wrap: wrap;
}
.product-qty-input {
  display: flex;
  border-radius: 7px;
  overflow: hidden;
  align-items: center;
}
.product-qty-input button {
  border: none;
  height: 40px;
  background-color: #f6f6f6;
  color: #434141 !important;
}

.product-qty-input .show-qty {
  background: #f6f6f6;
  border: none;
  height: 40px;
  text-align: center;
  display: flex;
  justify-content: center;
  width: 65px;
  outline: none;
  font-size: 18px;
  color: #434141;
  align-items: center;
  font-weight: 600;
}
form.product-pincode-input {
  display: flex;
  height: 40px;
}
form.product-pincode-input input {
  border: none;
  background: #f6f6f6;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 0 10px;
  font-size: 14px;
}
form.product-pincode-input button {
  border-radius: 0 5px 5px 0;
  border: none;
  background: #f6f6f6;
}

.table-c {
  width: 100%;
  overflow-x: scroll;
}

table.seller-price-table.pdt {
  /* padding-top: 10px; */
  margin-bottom: 25px;
  min-width: 694px;
  width: 100%;
  margin-top: 38px;
}
.pdt .tablerow-head {
  vertical-align: baseline;
  height: 40px;
  font-size: 15px;
  color: #006ff2;
}
.table-head {
  text-align: left;
}
.pdt .tablerow-data {
  vertical-align: unset;
  height: 40px;
  font-size: 15px;
  font-weight: 500;
}
.inp-td {
  width: 45px !important;
  vertical-align: top;
  height: 26px;
}
.table-data {
  width: 130px;
}

.price-heading {
  font-weight: 500;
  /* margin-bottom: 5px; */
  color: #520000;
}
.ite-price-container {
  margin: 10px 0 5px;
}
span.old-price {
  color: gray;
  text-decoration: line-through;
}
span.price {
  margin-right: 15px;
  font-size: 35px;
  font-weight: 500;
}
span.price-span {
  font-weight: 400;
  color: gray;
  font-size: 13px;
}
.discount-percentage {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 500;
  color: #006ff2;
}
.price-btn {
  margin-right: 15px;
  height: 36px;
  width: 143px;
  border-radius: 6px;
  border: none;
  background: #006ff2;
  color: white;
  font-weight: 600;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-size: 15px;
}
.price-desc {
  margin-bottom: 25px;
}

.logistics-table-container {
  padding-top: 5px;
  margin-bottom: 25px;
  width: 100%;
  overflow-x: scroll;
}

.logistics-table-container table.logistics-table {
  min-width: 695px;
  /* background: red; */
}
tr.logistics-headrow {
  height: 41px;
  background: #c6eeff;
  font-size: 12px;
}
th.logistics-head {
  border-spacing: 0;
  padding: 0 10px;
  text-align: center;
}
td.logistics-data {
  font-size: 12px;
  font-weight: 500;
  height: 30px;
  text-align: center;
}

.dd-box {
  width: 137px;
  border-radius: 6px !important;
  overflow: hidden;
}

.select-size {
  height: 37px;
  background: #f0f0f0;
  font-size: 15px !important;
  font-weight: 500 !important;
  outline: none !important;
}

@media screen and (max-width: 1000px) {
  .pd-divider {
    flex-direction: column;
  }

  .pd-divider .pd-left {
    width: 100%;
    padding: 0px;
    position: unset;
  }
  .pd-divider .pd-right {
    width: 100%;
    padding: 0px;
  }
  .pd-divider .pd-right .pd-right-details {
    width: 90%;
    padding: 15px;
  }
  .product-qty {
    margin-bottom: 25px;
  }
  /* .product-qty-pincode {
    flex-direction: column;
  } */
  .product-size {
    margin-bottom: 25px;
    width: 50%;
}

form.product-pincode-input {
  width: 100%;
}

form.product-pincode-input input {
  width: 100%;
}
.product-pincode {
  width: 100%;
}

}

@media screen and (max-width:550px){
.product-name {
    font-size: 25px;
    margin-bottom: 25px;
    font-weight: 500;
}

.price-btn-container {
  display: flex;
  justify-content: space-between;
}

button.price-btn {
  margin-right: 0px;
  height: 36px;
  width: 49%;
}

}

.type-selector{
  width: 110px;
  height: 31px;
  border-radius: 6px;
  padding-left: 8px;
  font-weight: 500;
  font-family: inherit;
  background: #f6f6f6;
  border: none;
  outline: none;
}
.infoic{position: relative; color: #000;}
.infoic:hover .infoic-box{display: block;}
.infoic-box{position: absolute; width: 300px; background-color: #fff;box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px; box-sizing: border-box; padding: 15px; border-radius: 10px; display: none;}
.infoic-box h5{margin: 0px; font-size: 17px; margin-bottom: 20px; font-family: 'Inter', sans-serif;}
.infoic-box h4{margin: 0px; font-size: 15px;font-weight: 600;font-family: 'Inter', sans-serif;}
.infoic-box p{margin-top: 0px; font-size: 13px; font-weight: 400;font-family: 'Inter', sans-serif;}
.table-cNoScroll{overflow-x: unset;}
.pdf-card{width: 25% !important; box-shadow: 0px 4px 10px rgb(0 0 0 / 25%);     border-radius: 10px;}
.pdf-card .pdf-header{width: 100% !important;  box-shadow: none; border-radius: 10px 10px 0px 0px;height: 220px; border-bottom: 1px solid #A5A2A2;}

.dfltCheck{background: #006FF2 !important;color: #fff;}

.RadioBTNbutton {
  float: left;
  margin: 0 5px 0 0;
  width: 100px;
  height: 40px;
  position: relative;
}

.RadioBTNbutton label,
.RadioBTNbutton input {
  background-color: #EFEFEF;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.RadioBTNbutton input[type="radio"] {
  opacity: 0.011;
  z-index: 100;
}

.RadioBTNbutton input[type="radio"]:checked + label {
  background: #006FF2;
  border-radius: 4px;
  color: #fff;
}

.RadioBTNbutton label {
  cursor: pointer;
  z-index: 90;
  line-height: 40px; text-align: center;
}
.tab2,.tab3{display: none;}


@media screen and  (max-width:600px){
  .pd-divider .pd-right .pd-right-details {
    width: 100%;
    padding: 15px;
}

.RadioBTNbutton {
  width: 118px;
  height: 38px;
}
.product-qty-pincode {
  align-items: flex-start;
}
.uni-heading > span{
  margin-top: 0;
  margin-bottom: 15px;
}


}