    padding: 20px 20px 20px 0;
}
.checkout-container .rt-container {
    width: 32%;
    padding: 20px 0 20px 20px;
}

.d_flex{
    width: inherit;
    display: flex !important;
    justify-content: space-between;
}
.mb-4{
    margin-bottom: 40px;
}
.mb_4{
    margin-bottom: 40px !important;
}
.mt-4{
    margin-top: 40px;
    margin-bottom: 40px;
}
.mt-1{
    margin-top: 10px !important;
}
.d-flex{
    display: flex !important;
}
.flex_row{
    flex-direction: row !important;
}
.ms-auto{
    margin-left: auto;
}
.text-end{
    text-align: end;
}
.mt-0{
    margin-top: 0 !important;
}
.checkout-btn{
    /* width: 300px; */
        width: 293px;
    text-align: start;
    padding: 8px 10px;
    border: 1px solid #7C7C7C;
    border-radius: 10px;
    background:  #fff;;
}
.checkout-btn:hover{
    background: #EEF6FF;

}
.checkout-btn:focus{
    background: #EEF6FF;

}
.amount{
    margin-bottom: 10px;
    color: #38D900;
    font-weight: 600;
    font-size: 28px;
    line-height: 30px;
    margin-top: 10px;

}
.text-gray{
    color: #989898;
}
.instr-list{
    list-style: decimal;
}
.instr-list li span{
    color:#006FF2;
    text-decoration: underline;
}
.instr-list li {
    margin-bottom: 20px;
}
.pay-btn{
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    border: none;
    font-size: 22px;
    font-weight: 500;
    color: white;
    background: #006FF2;
    border-radius: 5px;
    
}
.css-j204z7-MuiFormControlLabel-root{     margin-left: 0% !important;}
.product_card{
    /* width: 450px; */
    width: 100%;
    border-radius: 10px;
    padding: 0 15px 10px;
    border: 1px solid #C6C6C6;
    margin-bottom: 40px;
}
.checkout_product_img_container{
    padding: 10px;
}
.checkout_product_img{
    object-fit: cover;
    width: 100px;
    height: 116px;
    border: 1px solid #C6C6C6;
}
.checkout_product_img img{
    width: 100px;
    height: 116px;
}
.checkout_product_card_heading{
    margin: 20px 0;
}
.checkout_product_details{
    padding: 0 10px;
}
.checkout_product_details h3, .checkout_product_details p{
    color: #686767;
    
}
.checkout_product_details h3{
    margin: 20px 0;
}
.checkout_product_details p{
    margin-top: 0;
    margin-bottom: 20px;
}

.price_card{
    /* width: 450px; */
    width: 100%;
    border-radius: 10px;
    padding: 0 15px 10px;
    border: 1px solid #C6C6C6;
}
.price_card .price_details{
    display: flex;
    justify-content: space-between;
}
.price_card .price_details p{
    margin-top: 0;
    margin-bottom: 20px;
}


