.buyers-sidebar {
  position: fixed;
  top: 104px;
  z-index: 999;
  color: white;
  height: 81vh;
  width: 23%;
}

.sts-btn {
  position: absolute;
  bottom: 0;
  left: 0;
}

.buyers-sidebar-heading {
  font-size: 24px;
  padding-left: 15px;
  font-weight: 500;
  margin-bottom: 25px;
}
.css-113qkos-MuiSwitch-root .MuiSwitch-thumb {
  width: 19px;
  height: 19px;
}
.css-113qkos-MuiSwitch-root {
  width: 46px;
  height: 22px;
}

.buyers-options {
  font-size: 15px;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  /* height: 50px; */
  height: 40px;
  display: flex;
  align-items: center;
  /* padding-left: 15px; */
  padding-left: 8px;
  border-radius: 10px;
  margin-bottom: 6px;
  cursor: pointer;
  color: white;
  text-decoration: none;
}
.hideSubMenu a {
  color: #000;
}
.buyers-options.active-opn {
  background: white;
  color: #006ff2;
}

.hideSubMenu {
  background: #a2d2ff;
  border-radius: 10px;
  margin-left: 10px;
  display: none;
}

.callbtn {
  position: absolute;
  top: 102px;
  right: -50px;
  background: none;
  border: 2px solid;
  width: 50px;
  height: 50px;
  border-radius: 0 11px 11px 0;
  background: white;
  border: 2px solid #006ff2;
  color: #006ff2;
  background: transparent;
  cursor: pointer;
  display: none;
}
.css-j204z7-MuiFormControlLabel-root {
  margin-left: 44% !important;
}
