.vrn {
  background: #a2d2ff;
  color: #000;
  float: left; font-family: 'Inter', sans-serif;
  width: 26%;
  position: fixed;
  bottom: 0;
  height: 40px;
  left: 0px;
}
/* .vrn .css-113qkos-MuiSwitch-root{left: 52%;} */
.vrn label > span {
  /* left: 91%; */
  left: 85%;
}
.vrn label {
  margin-left: 10% !important;
}
.callbtn-i {
  transition: transfrom 0.4s ease;
}
.callbtn.active-callbtn .callbtn-i {
  transform: rotate(133deg);
}

@media screen and (max-width: 700px) {
  .callbtn {
    display: block;
  }

  .buyers-sidebar {
    width: 100%;
    position: fixed;
    top: 77px;
    left: -250px;
    z-index: 997;
    background: white;
    height: 100%;
    box-shadow: 0px -5px 6px rgb(0 0 0 / 50%);
    width: 228px;
    height: 100%;
    padding: 10px;
    transition: 0.2s ease;
    color: black;
  }
  .buyers-options {
    color: rgb(0, 0, 0);
  }
  .buyers-options.active-opn {
    background: #006ff2;
    color: white;
  }
}

.hehe-form {
  margin-right: 0 !important;
  display: flex !important;
  flex-direction: row-reverse !important;
  align-items: center !important;
  width: fit-content !important;
  padding-left: 9px !important;
  margin-top: 70px !important;
}

.hehe-form
  .css-113qkos-MuiSwitch-root
  .MuiSwitch-switchBase.Mui-checked
  + .MuiSwitch-track {
  background-color: #006ff2;
}

@media screen and (max-width: 700px) {
  .hehe-form {
    padding-left: 29px !important;
  }
}

.hideSubMenu:has(> a.active-opn) {
  display: block !important;
}
.buyers-options.active-opn + #MyAccount82 {
  display: block;
}
.menuAllIcons {
  float: left;
  margin-right: 10px;
  height: 23px;
}
.icon4blueShowHide {
  display: none;
}
.buyers-options.active-opn > .icon4blueShowHide {
  display: block;
}
.buyers-options.active-opn > .icon4WhiteShowHide {
  display: none;
}
