.approve-div {
    padding: 20px;
    width: 600px;
    margin-inline: auto;
}

.approve-div strong {
    font-size: 22px;
    margin-bottom: 30px;
    display: block;
}

.approve-form {
    /* padding: 20px; */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.approve-input-c {
    display: flex;
    width: 45%;
    flex-direction: column;
    margin-bottom: 36px;
}

.approve-input-c label {
    font-weight: 500;
}

.approve-input-c .label-2-g {
    font-size: 12px;
    font-weight: 400;
    color: grey;
}
.approve-input-c input {
    height: 34px;
    border-radius: 8px;
    outline: none;
    /* border: 0.9px solid black; */
    border: none;
    font-size: 15px;
    box-shadow: rgb(0 0 0 / 24%) 0px 1px 5px;
    margin-top: 10px;
    padding-left: 11px;
}


.approve-milestone{
    width: 100%;
}
.approve-milestone h1{
    margin-top: 0;
}
.approve-milestone .input-heading {
    width: 30%;
    text-align: center;
    color: #0a75f3;
    font-size: 15px;
    margin-bottom: 20px;
    font-weight: 500;
}

.approve-milestone  input.ir1 {
    width: 30%;
    text-align: center;
    font-weight: 500;
    border-radius: 5px;
    height: 31px;
    border: none;
    background: #ffffff;
    outline: none;
    margin-bottom: 20px;
    box-shadow: rgb(0 0 0 / 24%) 0px 1px 5px;
}

@media screen and (max-width:640px){
    .approve-div {
        padding: 20px;
        width: auto;
        margin-inline: auto;
    }
    .approve-input-c {
        display: flex;
        width: 100%;
        flex-direction: column;
        margin-bottom: 36px;
    }

    
} 