/*new switches design*/
.switchVKG {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switchVKG input {
  opacity: 0;
  width: 0;
  height: 0;
}

.sliderVKG {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.sliderVKG:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .sliderVKG {
  background-color: #2196f3;
}

input:focus + .sliderVKG {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .sliderVKG:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.sliderVKG.roundVKG {
  border-radius: 34px;
}

.sliderVKG.roundVKG:before {
  border-radius: 50%;
}
